#root {
    display: flex;
    align-items: center;
    justify-content: center;
    background: #E5E5E5;
}

.app__container {
    font-family: 'Raleway', sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0px;
    width: 1440px;
    background: #F5F5F5;
}

.header {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px;
    width: 100%;
    background: #FFFFFF;
}

.topo {
    display: flex;
    width: 1134px;
}

.topo__cabecalho {
    width: 100%;
    display: flex;
    flex-direction: row;
    padding: 16px 0px;
    align-items: center;
    justify-content: space-between;
}

.cabecalho__direita {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0px;
    grid-gap: 32px;
    gap: 32px;
}

.cabecalho__botao {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 16px;
    grid-gap: 10px;
    gap: 10px;
}

.cabecalho__botao__login {
    display: flex;
    cursor: pointer;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 16px;
    grid-gap: 10px;
    gap: 10px;
    width: 182px;
    height: 51px;
    background: none;
    border: 1px solid #2A7AE4;
    color: #2A7AE4;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
}


.cabecalho__botao__admin {
    display: flex;
    cursor: pointer;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 16px;
    grid-gap: 10px;
    gap: 10px;
    width: 182px;
    height: 51px;
    background: none;
    border: 1px solid #2A7AE4;
    color: #2A7AE4;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
}

.cabeca√ßho__pesquisa__lupa {
    display: none;


}

.cabecalho__lupa {
    align-items: center;
    width: 17.05px;
    height: 17.06px;
}



