.contact__form {
    font-family: 'Raleway';
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 0px;
    gap: 16px;
    width: 560px;
    height: 240px;
}

.full__input {
    display: inline-block;
    padding: 3px;
    background-color: white;
    width: 560px;
    height: 40px;
    border-radius: 4px;
    border: none;
    border-bottom: 1px solid #D8D8D8;
}


.contact__form__title {
    color: #464646;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px
}

.contact__form__name {
    width: 100%;
    background: #FFFFFF;
    border-radius: 4px;
    color: #464646;
    border: none;
    outline: 0;
    display: block;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    padding: 0 16px;
    -webkit-box-sizing: border-box;
    /* Safari/Chrome, other WebKit */
    -moz-box-sizing: border-box;
    /* Firefox, other Gecko */
    box-sizing: border-box;
    /* Opera/IE 8+ */
}

.contact__form__message {
    width: 565px;
    height: 82px;
    background: #FFFFFF;
    color: #464646;
    border-radius: 4px;
    border: none;
    outline: 0;
    display: block;
    resize: none;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    border-radius: 4px;
    border-bottom: 1px solid #D8D8D8;
    padding:16px;
    -webkit-box-sizing: border-box;
    /* Safari/Chrome, other WebKit */
    -moz-box-sizing: border-box;
    /* Firefox, other Gecko */
    box-sizing: border-box;
    /* Opera/IE 8+ */
}

.contact__form__submit {
    cursor: pointer;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 16px;
    gap: 10px;
    width: 165px;
    height: 51px;
    background: #2A7AE4;
    color: #FFFFFF;
    border: none;
}

label {
    padding: 0 8px;
    color: #A2A2A2;
    display: block;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
}

@media only screen and (max-width: 1260px) {
    .contact__form {
        width: 434px;
        height: 154px;
    }

    .full__input {
        width: 434px;
        height: 56px;
        border-radius: 4px;
    }

    .contact__form__message {
        width: 440px;
        height: 82px;
        border-radius: 4px;
    }


    .contact__form__submit {
        padding: 12px 16px;
        width: 151px;
        height: 40px;
    }

}

@media only screen and (max-width: 768px) {
    .contact__form {
        width: 328px;
        height: 229px;
    }

    .full__input {
        width: 328px;
        height: 56px;
        border-radius: 4px;
    }

    .contact__form__message {
        width: 334px;
        height: 82px;
        border-radius: 4px;
    }


    .contact__form__submit {
        padding: 12px 16px;
        width: 151px;
        height: 40px;
    }

}